        channel0Data = dvChannel0.getUint16(n) ^ keyHexadecimalNum.key1;
        channel1Data = dvChannel1.getUint16(n) ^ keyHexadecimalNum.key1;
        channel2Data = dvChannel2.getUint16(n) ^ keyHexadecimalNum.key1;
        channel3Data = dvChannel3.getUint16(n) ^ keyHexadecimalNum.key1;
        this.dvChannel0.setUint16(n, channel0Data);
        this.dvChannel1.setUint16(n, channel1Data);
        this.dvChannel2.setUint16(n, channel2Data);
